import React, { useEffect, useState } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import { deleteCart, updateCart } from '../Redux/Action/ActionCart';
import ListCart from './Component/ListCart';
import alertify from 'alertifyjs';
import { Link, Redirect } from 'react-router-dom';
import CartAPI from '../API/CartAPI';
import queryString from 'query-string';
import convertMoney from '../convertMoney';

function Cart(props) {
  //id_user c ly t redux
  const id_user = useSelector((state) => state.Cart.id_user);

  //listCart c ly t redux
  const listCart = useSelector((state) => state.Cart.listCart);

  const [cart, setCart] = useState([]);

  const [total, setTotal] = useState();

  const dispatch = useDispatch();

  //State dng  Load d liu t Redux
  const [loadRedux, setLoadRedux] = useState({
    idProduct: '',
    count: '',
  });

  //State dng  Load d liu t API
  const [loadAPI, setLoadAPI] = useState(false);

  //Hm ny dng  Load d liu  Redux
  //Khi ngi dng cha ng nhp
  // useEffect(() => {
  //   console.log(id_user);
  //   const fetchDataRedux = () => {
  //     if (!localStorage.getItem('id_user')) {
  //       setCart(listCart);

  //       getTotal(listCart);
  //     }
  //   };

  //   fetchDataRedux();
  // }, [loadRedux]);

  //Hm ny dng  tnh tng tin carts
  function getTotal(carts) {
    let sub_total = 0;

    const sum_total = carts.map((value) => {
      return (sub_total +=
        parseInt(value.productId.price) * parseInt(value.quantity));
    });

    setTotal(sub_total);
  }

  //Hm ny dng  load d liu t API
  //Khi ngi dng  ng nhp
  useEffect(() => {
    const fetchData = async () => {
      if (localStorage.getItem('id_user')) {
        const params = {
          idUser: localStorage.getItem('id_user'),
        };

        const query = '?' + queryString.stringify(params);

        console.log(query);

        const response = await CartAPI.getCarts(query);
        console.log(response);
        if (response.data.error) {
          alertify.set('notifier', 'position', 'bottom-left');
          alertify.error('Your must signin to use your cart!');
        } else {
          setCart(response.data.products);

          getTotal(response.data.products);
        }
      } else {
        alertify.set('notifier', 'position', 'bottom-left');
        alertify.error('Your must signin to use your cart!');
      }
    };

    fetchData();

    setLoadAPI(false);
  }, [loadAPI]);

  //Hm ny dng  truyn xung cho component con x v tr ngc d liu li component cha
  const onDeleteCart = (getProduct) => {
    console.log(getProduct);

    if (localStorage.getItem('id_user')) {
      // user  ng nhp

      //Sau khi nhn c d liu  component con truyn ln th s gi API x l d liu
      const fetchDelete = async () => {
        const params = {
          idProduct: getProduct,
        };

        const query = '?' + queryString.stringify(params);

        const response = await CartAPI.deleteToCart(query);
        if (response.data.error) {
          alertify.set('notifier', 'position', 'bottom-left');
          alertify.error('Your must signin to use your cart!');
        } else {
          console.log(response);
          alertify.set('notifier', 'position', 'bottom-left');
          alertify.error('Bn  Xa Hng Thnh Cng!');
        }
      };

      fetchDelete();

      //Sau  thay i state loadAPI v load li hm useEffect
      setLoadAPI(true);
    } else {
      // user cha ng nhp

      //Nu khng c phin lm vic ca Session User th mnh s x l vi Redux
      const data = {
        idProduct: getProduct,
        // idUser: getUser,
      };

      //a d liu vo Redux
      const action = deleteCart(data);
      dispatch(action);

      alertify.set('notifier', 'position', 'bottom-left');
      alertify.error('Bn  Xa Hng Thnh Cng!');

      //set state loadRedux  n load li hm useEffect  tip tc ly d liu t redux
      setLoadRedux({
        idProduct: getProduct,
        count: '',
      });
    }
  };

  //Hm ny dng  truyn xung cho component con x v tr ngc d liu li component cha
  const onUpdateCount = (getProduct, getCount) => {
    console.log('Count: ' + getCount + ', idProduct: ' + getProduct);

    if (localStorage.getItem('id_user')) {
      // user  ng nhp

      //Sau khi nhn c d liu  component con truyn ln th s gi API x l d liu
      const fetchPut = async () => {
        const params = {
          //   idUser: getUser,
          idProduct: getProduct,
          count: getCount,
        };

        const query = '?' + queryString.stringify(params);

        const response = await CartAPI.putToCart(query);
        if (response.data.error) {
          alertify.set('notifier', 'position', 'bottom-left');
          alertify.error('Your must signin to use your cart!');
        } else {
          console.log(response);
          alertify.set('notifier', 'position', 'bottom-left');
          alertify.success('Bn  Sa Hng Thnh Cng!');
        }
      };

      fetchPut();

      //Sau  thay i state loadAPI v load li hm useEffect
      setLoadAPI(true);
    } else {
      //Nu khng c phin lm vic ca Session User th mnh s x l vi Redux
      const data = {
        idProduct: getProduct,
        // idUser: getUser,
        count: getCount,
      };

      //a d liu vo Redux
      const action = updateCart(data);
      dispatch(action);

      alertify.set('notifier', 'position', 'bottom-left');
      alertify.success('Bn  Sa Hng Thnh Cng!');

      //set state loadRedux  n load li hm useEffect  tip tc ly d liu t redux
      setLoadRedux({
        idProduct: getProduct,
        count: getCount,
      });
    }
  };

  //Hm ny dng  redirect n page checkout
  const [redirect, setRedirect] = useState(false);

  const onCheckout = () => {
    if (!localStorage.getItem('id_user')) {
      alertify.set('notifier', 'position', 'bottom-left');
      alertify.error('Vui Lng Kim Tra Li ng Nhp!');
      return;
    }

    if (cart.length === 0) {
      alertify.set('notifier', 'position', 'bottom-left');
      alertify.error('Vui Lng Kim Tra Li Gi Hng!');
      return;
    }

    setRedirect(true);
  };

  return (
    <div className="container">
      <section className="py-5 bg-light">
        <div className="container">
          <div className="row px-4 px-lg-5 py-lg-4 align-items-center">
            <div className="col-lg-6">
              <h1 className="h2 text-uppercase mb-0">Cart</h1>
            </div>
            <div className="col-lg-6 text-lg-right">
              <nav aria-label="breadcrumb">
                <ol className="breadcrumb justify-content-lg-end mb-0 px-0">
                  <li className="breadcrumb-item active" aria-current="page">
                    Cart
                  </li>
                </ol>
              </nav>
            </div>
          </div>
        </div>
      </section>
      <section className="py-5">
        <h2 className="h5 text-uppercase mb-4">Shopping cart</h2>
        <div className="row">
          <div className="col-lg-8 mb-4 mb-lg-0">
            <ListCart
              listCart={cart}
              onDeleteCart={onDeleteCart}
              onUpdateCount={onUpdateCount}
            />

            <div className="bg-light px-4 py-3">
              <div className="row align-items-center text-center">
                <div className="col-md-6 mb-3 mb-md-0 text-md-left">
                  <Link
                    className="btn btn-link p-0 text-dark btn-sm"
                    to={`/shop`}
                  >
                    <i className="fas fa-long-arrow-alt-left mr-2"> </i>
                    Continue shopping
                  </Link>
                </div>
                <div className="col-md-6 text-md-right">
                  {redirect && <Redirect to={'/checkout'} />}
                  <span
                    className="btn btn-outline-dark btn-sm"
                    onClick={onCheckout}
                  >
                    Proceed to checkout
                    <i className="fas fa-long-arrow-alt-right ml-2"></i>
                  </span>
                </div>
              </div>
            </div>
          </div>
          <div className="col-lg-4">
            <div className="card border-0 rounded-0 p-lg-4 bg-light">
              <div className="card-body">
                <h5 className="text-uppercase mb-4">Cart total</h5>
                <ul className="list-unstyled mb-0">
                  <li className="d-flex align-items-center justify-content-between">
                    <strong className="text-uppercase small font-weight-bold">
                      Subtotal
                    </strong>
                    <span className="text-muted small">
                      {convertMoney(total)} VND
                    </span>
                  </li>
                  <li className="border-bottom my-2"></li>
                  <li className="d-flex align-items-center justify-content-between mb-4">
                    <strong className="text-uppercase small font-weight-bold">
                      Total
                    </strong>
                    <span>{convertMoney(total)} VND</span>
                  </li>
                </ul>
              </div>
            </div>
          </div>
        </div>
      </section>
    </div>
  );
}

export default Cart;
